const validate = (values) => {
    const errors = {};
    if (!values.title) {
        errors.title = "Khng c  trng";
    } else if (values.title.length < 3) {
        errors.title = "Phi c nhiu hn 3 k t";
    }
    if(!values.price){
      errors.price = "Khng c  trng";
    }
    if(!values.image){
      errors.image ="Khng c  trng";
    }
    return errors;
};

export default validate;
